<template>
		<div class="file-drop-zone text-center" v-on:dragenter="dropIn" v-on:dragover="dropIn" v-on:dragleave="dropOut" v-on:drop="dropFile">
			<div class="drop-zone-file-select" v-show="!selectedPackageFile" @click="openFileSelector">
				<span class="select-file-text">
					<label for="select-package">Select File</label>
					<label>(.deb)</label>
					
					<input type="file" ref="fileSelector" @change="fileSelected" accept="application/vnd.debian.binary-package, application/x-deb">
				</span>
			</div>
			<div class="icon-preview" @click="clearPackageFile" v-show="selectedPackageFile">
				<img src="/resources/archive.png">
			</div>
		</div>
</template>

<style lang="less" scoped>
.icon-preview {
	box-shadow: inset 0 0 0 5px #434857;
	
	img {
		width: 100%;
		height: 100%;
	}
}
</style>

<script>
export default {
	name: "PackageFileSelector",
	props: ["value"],
	data() {
		return {
			selectedPackageFile: this.$props.value
		}
	},
	methods: {
		dropIn(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.add("drop-in");
		},
		dropOut(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.remove("drop-in");
		},
		dropFile(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.remove("drop-in");
			
			let file = e.dataTransfer.files[0];
			if (["application/vnd.debian.binary-package", "application/x-deb"].includes(file.type)) {
				this.selectedPackageFile = file;
				this.$emit('input', this.selectedPackageFile);
			}
		},
		
		openFileSelector() {
			this.$refs["fileSelector"].click();
		},
		clearPackageFile() {
			this.selectedPackageFile = null;
			this.$emit('input', this.selectedPackageFile);
		},
		fileSelected(e) {
			let file = e.target.files[0];
			
			this.selectedPackageFile = file;
			this.$emit('input', this.selectedPackageFile);
		}
	}
}
</script>